import React, { lazy, Suspense } from 'react';
import { Redirect, Route, Switch, useLocation } from 'react-router-dom';

const Clients = lazy(() =>
  import(/* webpackChunkName: "views-client-all" */ './clients')
);
const ClientDetails = lazy(() =>
  import(/* webpackChunkName: "viwes-client-one" */ './client-data')
);

const Client = ({ match }) => {
  const { pathname } = useLocation();

  return (
    <Suspense fallback={<div className="loading" />}>
      <Switch>
        <Redirect from="/*(/+)" to={pathname.slice(0, -1)} />
        <Route
          path={`${match.url}/:id`}
          exact
          render={(props) => <ClientDetails {...props} />}
        />
        <Route
          path={`${match.url}/`}
          exact
          render={(props) => <Clients {...props} />}
        />
      </Switch>
    </Suspense>
  );
};

export default Client;
